import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import './css/app.min.css';
import GreetingCard from "./routes/GreetingCard";
import Home from "./routes/Home";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/card/:name">
          <GreetingCard />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
