import React, { Component } from "react";
import axios from "axios";

export default class Friends extends Component {
  state = {
    endpoint: "https://idul-fitri.herokuapp.com/api/v1",
    key: "3dcce7ab-d511-4cfe-a62e-b3089444ee4d",
    friends: []
  };

  componentDidMount() {
    const container = document.querySelector("#friends-list");

    let startY;
    let startX;
    let scrollLeft;
    let scrollTop;
    let isDown;

    container.addEventListener("mousedown", (e) => mouseIsDown(e));
    container.addEventListener("mouseup", (e) => mouseUp(e));
    container.addEventListener("mouseleave", (e) => mouseLeave(e));
    container.addEventListener("mousemove", (e) => mouseMove(e));

    function mouseIsDown(e) {
      isDown = true;
      startY = e.pageY - container.offsetTop;
      startX = e.pageX - container.offsetLeft;
      scrollLeft = container.scrollLeft;
      scrollTop = container.scrollTop;
    }
    function mouseUp(e) {
      isDown = false;
    }
    function mouseLeave(e) {
      isDown = false;
    }
    function mouseMove(e) {
      if (isDown) {
        e.preventDefault();
        //Move vertcally
        const y = e.pageY - container.offsetTop;
        const walkY = y - startY;
        container.scrollTop = scrollTop - walkY;

        //Move Horizontally
        const x = e.pageX - container.offsetLeft;
        const walkX = x - startX;
        container.scrollLeft = scrollLeft - walkX;
      }
    }

    axios.get(`${this.state.endpoint}/greeting`, {
      params: {
        _apikey: this.state.key,
        where: {
          status: 1
        },
        order: ['id', 'DESC']
      }
    }).then(res => {
      this.setState({
        friends: res.data
      })
      console.log(this.state.friends)
    }).catch(e => console.log(e))

  }

  render() {
    return (
      <section id="section-friends" className={"section-friends " + this.props.className}>
        <div className="friends-list" id="friends-list">
          <div className="friends-list-inner">
            {
              this.state.friends.map(function(val, key){
                return (
                  <div className="friends-list-item cursor-pointer">
                    <div className="d-flex justify-content-center mb-3">
                      <div className="fs-41 h-80 w-80 rounded-circle friends-list-image d-flex align-items-center justify-content-center line-height-1 font-weight-700 text-uppercase">
                        {val.from.charAt(0)}
                      </div>
                    </div>
                    <p className="fs-31 text-white-50 m-0 line-height-1 text-left">
                      <i className="ri-double-quotes-l"></i>
                    </p>
                    <p className="fs-14 fs-md-14 my-2 long-content">
                      {val.message}
                    </p>
                    <p className="fs-31 text-white-50 mb-2 line-height-1 text-right">
                      <i className="ri-double-quotes-r"></i>
                    </p>
                    <p className="fs-14 fs-md-14 mb-0 font-weight-700 long-content">
                      ― {val.from}
                    </p>
                  </div>
                )
              })
            }
          </div>
        </div>
      </section>
    );
  }
}
