import React, { Component } from "react";
import template from "../img/greeting-card.png";
import html2canvas from "html2canvas";
import alertrict from "alertrict";

export default class Card extends Component {
  renderImage = () => {
    return html2canvas(document.querySelector("#greeting-card"), {}).then(
      (canvas) => {
        canvas.style.display = "none";
        var image = canvas.toDataURL("png");
        var a = document.querySelector("#download-greeting-card");
        a.setAttribute(
          "download",
          `Selamat hari raya idul fitri ${this.props.username}.png`
        );
        a.setAttribute("href", image);
        a.disabled = false;
      }
    );
  };

  copyUrl(event) {
    var dummy = document.createElement("input"),
      text = window.location.href;

    document.body.appendChild(dummy);
    dummy.value = text;
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);

    alertrict({
      title: "Berhasil!",
      text: "Link berhasil disalin ke klipboard.",
      showCancel: false,
    });
  }
  render() {
    return (
      <section id="section-card" className="section-card">
        <div className="wrapper d-flex align-items-center justify-content-center">
          <div className="section-card-inner">
            <div className="section-card-image" id="greeting-card">
              <img
                src={template}
                alt="Greeting card"
                onLoad={this.renderImage}
              />
              <div className="section-card-name font-weight-600 fs-21 fs-md-26 fs-lg-31">
                {this.props.username}
              </div>
            </div>
            <div className="mt-4">
              <a
                href="" 
                id="download-greeting-card"
                className="btn btn-sm btn-success d-inline-flex align-items-center mx-2"
                disabled={true}
              >
                <i className="ri-download-line"></i>&nbsp;&nbsp;Download
              </a>
              <button
                type="button"
                onClick={this.copyUrl}
                className="btn btn-sm btn-primary d-inline-flex align-items-center mx-2"
              >
                <i className="ri-clipboard-fill"></i>&nbsp;&nbsp;Salin link
              </button>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
