import React, { Component } from 'react';
import Fireworks from "./Fireworks";
import Typed from 'typed.js';

export default class Hero extends Component{
    componentDidMount() {
        this.typed()
    }

    typed(){
        var options = {
            strings: ['Minal aidin wal faizin 👋.', 'Mohon maaf lahir dan batin 🤝.'],
            typeSpeed: 30,
            loop: true,
            backDelay: 3000,
            fadeOut: false,
        }
          
        new Typed('#typed', options)
    }

    render(){
        return (
            <section id="section-hero" className="section-hero">
                <Fireworks/>
                <div className="hero-inner d-flex align-items-center justify-content-center">
                    <div className="wrapper">
                        <div className="row">
                            <div className="col-12">
                                <h2 className="text-center fs-18 fs-md-26 font-weight-400 mb-0">
                                    Rifki dan keluarga mengucapkan
                                </h2>
                                <h1 className="text-center my-2 fs-28 fs-md-51">
                                    Selamat hari raya Idul Fitri
                                </h1>
                                <h3 className="text-center fs-16 fs-md-18 font-weight-400 mb-0">
                                    🙋‍♂️ <span id="typed"></span>
                                </h3>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}