import React, { Component } from 'react';
import { Fireworks as fw } from 'fireworks-js';

export default class Fireworks extends Component{
    componentDidMount() {
        this.fireworksSetup()
    }

    fireworksSetup(){
        const container = document.querySelector('.fireworks-container')
        const fireworks = new fw({
            target: container,
            hue: 120,
            startDelay: 1,
            minDelay: 20,
            maxDelay: 30,
            speed: 4,
            acceleration: 1.05,
            friction: 0.98,
            gravity: 1,
            particles: 75,
            trace: 3,
            explosion: 5,
            boundaries: {
                top: 50,
                bottom: container.clientHeight,
                left: 50,
                right: container.clientWidth
            },
            sound: {
                enable: false,
                list: [
                    'explosion0.mp3',
                    'explosion1.mp3',
                    'explosion2.mp3'
                ],
                min: 4,
                max: 8
            }
        })

        fireworks.start()
    }

    render(){
        return (
            <div className="fireworks-container"></div>
        )
    }
}